import { useDispatch, useSelector } from 'react-redux'
import { Redirect, Link } from 'react-router-dom'
import { ChevronLeft } from 'react-feather'
import InputPassword from '@components/input-password-toggle'
import UILoader from '@components/ui-loader'
import { Card, CardBody, CardTitle, CardText, Form, FormGroup, Label, Button } from 'reactstrap'
import '@styles/base/pages/page-auth.scss'

import { resetPassowrd, resetPasswordChange } from '@store/ResetPassword/actions'

const ResetPassword = (props) => {
  const { token } = props.match.params

  const { authenticated } = useSelector(store => store.authentication),
    { resetFormData, formErrors, isLoading } = useSelector(store => store.resetPassword),
    dispatch = useDispatch()

  const handleFormChange = (name, value) => {
    dispatch(resetPasswordChange(name, value))
  }

  const handleResetPassword = () => {
    dispatch(resetPassowrd(token))
  }

  const handleSubmit = event => {
    event.preventDefault()
    handleResetPassword()
  }

  const _onChange = e => {
    handleFormChange(e.target.name, e.target.value)
  }

  if (!token) return <Redirect to='/login' />
  if (authenticated) return <Redirect to='/dashboard' />

  return (
    <UILoader blocking={isLoading}>
      <div className='auth-wrapper auth-v1 px-2'>
        <div className='auth-inner py-2'>
          <Card className='mb-0'>
            <CardBody>
              <Link className='brand-logo' to='/' onClick={e => e.preventDefault()}>
                <h2 className='brand-text text-primary ml-1'>Carhoot</h2>
              </Link>
              <CardTitle tag='h4' className='mb-1'>
                Reset Password
              </CardTitle>
              <CardText className='mb-2'>Your new password must be different from previously used passwords</CardText>
              <Form className='auth-reset-password-form mt-2' onSubmit={handleSubmit}>
                <FormGroup>
                  <Label className='form-label' for='new-password'>
                    New Password
                  </Label>
                  <InputPassword
                    className='input-group-merge'
                    id='new-password'
                    name={'password'}
                    placeholder={'Password'}
                    value={resetFormData.password}
                    onChange={(e) => {
                      _onChange(e)
                    }}
                    autoFocus
                  />
                  <span className="text-danger">{formErrors['password'] && formErrors['password'][0]}</span>
                </FormGroup>
                <FormGroup>
                  <Label className='form-label' for='confirm-password'>
                    Confirm Password
                  </Label>
                  <InputPassword
                    className='input-group-merge'
                    id='confirm-password'
                    name={'confirmPassword'}
                    placeholder={'Confirm Password'}
                    value={resetFormData.confirmPassword}
                    onChange={(e) => {
                      _onChange(e)
                    }}
                  />
                  <span className="text-danger">{formErrors['confirmPassword'] && formErrors['confirmPassword'][0]}</span>
                </FormGroup>
                <Button.Ripple color='primary' block type={'submit'}>
                  Set New Password
                </Button.Ripple>
              </Form>
              <p className='text-center mt-2'>
                <Link to='/Login/Login'>
                  <ChevronLeft className='mr-25' size={14} />
                  <span className='align-middle'>Back to login</span>
                </Link>
              </p>
            </CardBody>
          </Card>
        </div>
      </div>
    </UILoader>
  )
}

const mapStateToProps = state => {
  return {
    authenticated: state.authentication.authenticated,
    resetFormData: state.resetPassword.resetFormData,
    formErrors: state.resetPassword.formErrors
  }
}

export default ResetPassword
